@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2:wght@400;500;600;700;800&display=swap);
body{font-family:"Baloo Chettan 2",sans-serif;font-weight:400;font-size:20px}.sidenav{height:100%;width:220px;position:fixed;z-index:1;top:0;left:0;background-color:#a4ffb5;overflow-x:hidden;padding-top:20px}.sidenav .heading{padding:36px 16px 86px 36px}.sidenav .heading h1{font-size:60px}.sidenav .heading h1,.sidenav .heading h4{margin:0;padding:0}.sidenav a{padding:6px 8px 6px 36px;text-decoration:none;font-size:25px;color:#363636;display:block;transition:.2s ease-in-out}.sidenav a:hover{color:#696969;transition:.2s ease-in-out}.main{margin:100px 0 50px 220px;padding:0 10px;position:absolute}.headshot{max-width:70%;border-radius:50%;display:block;margin:auto auto 20px}.split:after{content:"";display:table;clear:both}.left{float:left;width:40%}.right{float:left;width:60%}.aboutcontact{margin-top:40px}.info{text-align:center;font-size:25px}.college{font-weight:800}.nameTitle{padding-bottom:20px}.contact{margin:30px 0 0 0}.contact .links{margin:10px 0 0 0}.contact .links img{max-width:40px}.contact a{margin:10px 15px 10px 0}.contact img{max-width:100%}.contact .right{margin-top:70px}.cards{display:flex;flex-wrap:wrap}.card{width:300px;margin:30px}.card-img-top{width:200px;display:block;margin:20px auto}@media screen and (max-height:450px){.sidenav{padding-top:15px}.sidenav a{font-size:18px}}
